<template>
  <div id="new-employe">
      <h3>Nuevo empleado</h3>
      <div class="row ">
          
          <form @submit.prevent="createEmployee" class="col s12">
              <div class="row">
                  <div class="input-field col s12">
                      <label for="employee-id">Id del empleado</label>
                      <input type="text" id="employee-id" v-model="employee_id" required>
                  </div>
              </div>
              <div class="row">
                  <div class="input-field col s12">
                      <label for="employee-name">Nombre</label>
                      <input type="text" id="employee-name" v-model="name" required>
                  </div>
              </div>
              <div class="row">
                  <div class="input-field col s12">
                      <label for="employee-dept">Departamento</label>
                      <input type="text" id="employee-dept" v-model="dept" required>
                  </div>
              </div>
              <div class="row">
                  <div class="input-field col s12">
                      <label for="employee-position">Posición</label>
                      <input type="text" id="employee-position" v-model="position" required>
                  </div>
              </div>
                  <button type="submit" class="btn teal">Crear</button>
                  <router-link to="/" class="btn red">Cancelar</router-link>
          </form>
          </div>
      </div>
</template>

<script>
import db from './firebaseInit'
    export default {
        name: 'new-employe',
        data () {
            return {
                employee_id: '',
                name: '',
                dept: '',
                position: ''
            }
        },
        methods: {
            createEmployee () {
                db.collection('employes').add({
                    employe_id: this.employee_id,
                    name: this.name,
                    dept: this.dept,
                    position: this.position
                })
                .then(docRef => {
                    this.$router.push('/')
                    toastr.success('Creado correctamente')
                })
                .catch(error => {
                    console.log(error)
                })
            }
        }
    }
</script>



