<template>
  <div id="edit-employee">
      <h3>Editar empleado: <small>{{name}}</small></h3>
      <div class="row ">
          
          <form @submit.prevent="updateEmployee" class="col s12">
              <div class="row">
                  <div class="col s12">
                      <label for="employee-id">Id del empleado</label>
                  </div>
                  <div class="input-field col s12">
                      <input type="text" id="employee-id" v-model="employee_id" disabled>
                  </div>
              </div>
              <div class="row">
                  <div class="col s12">
                      <label for="employee-name">Nombre</label>
                  </div>
                  <div class="input-field col s12">
                      <input type="text" id="employee-name" v-model="name" required>
                  </div>
              </div>
              <div class="row">
                  <div class="col s12">
                      <label for="employee-dept">Departamento</label>
                  </div>
                  <div class="input-field col s12">
                      <input type="text" id="employee-dept" v-model="dept" required>
                  </div>
              </div>
              <div class="row">
                  <div class="col s12">
                      <label for="employee-position">Posición</label>
                  </div>
                  <div class="input-field col s12">
                      <input type="text" id="employee-position" v-model="position" required>
                  </div>
              </div>
                  <button type="submit" class="btn teal">Actualizar</button>
                  <router-link to="/" class="btn red">Cancelar</router-link>
          </form>
          </div>
  </div>
</template>

<script>
import db from './firebaseInit'
    export default {
        name: 'edit-employee',
        data () {
            return {
                employee_id: '',
                name: '',
                dept: '',
                position: ''
            }
        },
        beforeRouteEnter: (to, from, next) => {
            db.collection('employes').where('employe_id', '==', to.params.employee_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    next(vm => {
                        vm.employee_id = doc.data().employe_id
                        vm.name = doc.data().name
                        vm.dept = doc.data().dept
                        vm.position = doc.data().position
                    })
                })
            })
        },
        watch: {
            '$route': 'fetchData'
        },
        methods: {
            fetchData () {
                db.collection('employes').where('employe_id',
                '==', this.$route.params.employee_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.employee_id = doc.data().employe_id
                        this.name = doc.data().name
                        this.dept = doc.data().dept
                        this.position = doc.data().position
                    })
                })
            },
            updateEmployee () {
                db.collection('employes').where('employe_id',
                '==', this.$route.params.employee_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.update({
                            employe_id: this.employee_id,
                            name: this.name,
                            dept: this.dept,
                            position: this.position
                        })
                        .then(() => {
                            this.$router.push({name: 'view-employee', 
                            params: {employee_id: this.employee_id}})
                            toastr.success('Actualizado correctamente')
                        })  
                    })
                })
            }
        }
    }
</script>



