<template>
  <div id="view-employee">
    <h3>{{name}}</h3>

    <table class="highlight responsive-table bordered centered">
          <caption ><strong>Detalles</strong> </caption>
        <thead>
          <tr>
              <th>#ID del empleado</th>
              <th>Departamento</th>
              <th>Posición</th>
              <th>Acciones</th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td>{{employee_id}}</td>
            <td>{{dept}}</td>
            <td>{{position}}</td>
            <td>
                <button @click="deleteEmployee" class="btn red"><i class="fa fa-trash" aria-hidden="true"></i></button>
                <router-link :to="{name: 'edit-employee', params: {employee_id: employee_id}}" class="btn green"><i class="fa fa-pencil" aria-hidden="true"></i></router-link>
            </td>

          </tr>
          
        </tbody>
      </table>
      <br>

        <router-link to="/" class="btn blue">
            <i class="fa fa-chevron-left" aria-hidden="true"></i> Atrás
        </router-link>
      
  </div>
</template>

<script>
import db from './firebaseInit'
    export default {
        name: 'view-employee',
        data () {
            return {
                employee_id: null,
                name: null,
                dept: null,
                position: null
            }
        },
        beforeRouteEnter: (to, from, next) => {
            db.collection('employes').where('employe_id', '==', to.params.employee_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    next(vm => {
                        vm.employee_id = doc.data().employe_id
                        vm.name = doc.data().name
                        vm.dept = doc.data().dept
                        vm.position = doc.data().position
                    })
                })
            })
        },
        watch: {
            '$route': 'fetchData'
        },
        methods: {
            fetchData () {
                db.collection('employes').where('employe_id',
                '==', this.$route.params.employee_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.employee_id = doc.data().employe_id
                        this.name = doc.data().name
                        this.dept = doc.data().dept
                        this.position = doc.data().position
                    })
                })
            },
            deleteEmployee () {
                if (confirm('¿Estás seguro de elminarlo?')) {
                    db.collection('employes').where('employe_id', '==',
                    this.$route.params.employee_id).get()
                    .then(querySnapshot => {
                        querySnapshot.forEach(doc => {
                            doc.ref.delete()
                            this.$router.push('/')
                        })
                    })
                    toastr.info('Eliminado correctamente');
                }
            },
            editEmployee () {
                
            }
        }
    }
</script>



