<template>
  <div id="dashboard">
      <h3>Dashboard</h3>

       <!--<ul class="collection with-header">
        <li class="collection-header"><h4>Empleados</h4></li>
        <li class="collection-item" v-for="employee in employees" :key="employee.id">
         {{employee.employee_id}} | {{employee.name}} <div class="chip"> {{employee.dept}} </div>

         <router-link class="secondary-content" :to="{name: 'view-employee', params: {employee_id: employee.employee_id}}">
             <i class="fa fa-eye small pink-text" aria-hidden="true"></i>
         </router-link>
        </li>
        
      </ul> -->
        
      <table class="highlight responsive-table bordered centered">
          <caption ><strong>Lista de empleados</strong> </caption>
        <thead>
          <tr>
              <th>#</th>
              <th>Nombre</th>
              <th>Departamento</th>
              <th>Mostrar detalles</th>
          </tr>
        </thead>

        <tbody>
          <tr v-for="employee in employees" :key="employee.id">
            <td>{{employee.employee_id}}</td>
            <td>{{employee.name}}</td>
            <td>{{employee.dept}}</td>
            <td>
                <router-link :to="{name: 'view-employee', params: {employee_id: employee.employee_id}}">
                    <i class="fa fa-eye small pink-text" aria-hidden="true"></i>
                </router-link>
            </td>
          </tr>
          
        </tbody>
      </table>

      <div class="fixed-action-btn">
    <router-link to="/nuevo" class="btn-floating btn-large teal">
      <i class="fa fa-plus" aria-hidden="true"></i>
   </router-link>
    
  </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: "dashboard",
  data() {
    return {
        employees: []
    }
  },
  created () {
      db.collection('employes').orderBy('dept').get()
      .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              
              const data = {
                  'id': doc.id,
                  'employee_id': doc.data().employe_id,
                  'name': doc.data().name,
                  'dept': doc.data().dept,
                  'position': doc.data().position
              }
              this.employees.push(data)
          })
      })
  }
}
</script>



